/**
 * ChatRedirect - Compatibility route for legacy chat URLs.
 *
 * Old sidebar / deep links may navigate to:
 *   /cases/:caseId/chat?session=...
 *
 * NOTEBOOKLM is now the default at:
 *   /cases/:caseId?tab=chat&session=...
 */
import React from 'react';
import { Navigate, useLocation, useParams } from 'react-router-dom';

export default function ChatRedirect(): JSX.Element {
  const { caseId } = useParams<{ caseId: string }>();
  const location = useLocation();

  if (!caseId) {
    return <Navigate to="/cases" replace />;
  }

  const params = new URLSearchParams(location.search);
  // Force NOTEBOOKLM chat tab
  params.set('tab', 'chat');

  const qs = params.toString();
  return <Navigate to={`/cases/${caseId}${qs ? `?${qs}` : ''}`} replace />;
}

